.contAdmin {
    --color_bordes: rgb(48, 48, 48);
    --alto_panel: 6rem;
    position: relative;
    padding: 2rem;
    padding-top: var(--alto_panel);
    background-color: white;
}

.adminOptionsPanel {
    width: 100%;
    height: var(--alto_panel);
    position: absolute;
    top: 0;
    left: 0;
    padding: 1rem 0;
}

.botonesAdmin {
    width: fit-content;
    background-color:  rgb(72, 153, 219);
    border: 1px solid rgb(44, 44, 44);
}

.allProductsTable {
    background-color: white !important;
    color: var(--color_bordes);
    border: 1px solid var(--color_bordes);
    width: 100%;
    max-width: 100%;
}

.allProductsTable th, .allProductsTable td {
    border: 1px solid var(--color_bordes);
    text-align: center;
    vertical-align: center;
    padding: 0.25rem;
    font-size: 0.90rem;
    width: auto; 
    word-break: normal;     /* Pasa palabras a una segunda linea */
}

.allProductsTable th {
    background-color: rgb(72, 153, 219);
    color: white;
    font-weight: 400;
    word-break: unset;
}

.allProductsTable td {
    cursor: pointer;
    position: relative;
}

.allProductsTable td:hover {
    background-color: rgb(187, 223, 255);
}

.imgProductosAdmin {
    width: 5rem;
}

.inputCeldasAdmin {
    all: unset !important;
    width: 100% !important;   
    height: 100px !important; 
    overflow: hidden !important;
}

.checkAdmin {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 1rem;
}

.cancelAdmin {
    right: unset;
    left: 0;
    filter: hue-rotate(0);
}

.deleteAdmin {
    left: 0;
    margin: auto;
    height: 1.5rem;
}

.checkAdmin:hover {
    filter: brightness(150%);
}

.contDeleteAdmin {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    height: 1.5rem;
}

.contDeleteAdmin:hover::before {
    content: "Eliminar Producto";
    position: absolute;
    left: 80%;
    bottom: 100%;
    background-color: rgb(206, 66, 66);
    border: 1px solid var(--color_bordes);
    padding: 5px;
    border-radius: 5px;
    color: white;
    z-index: 100;
}

.allProductsTable thead{
    position: sticky;
    top: 142px;
    z-index: 100;
}

.columnaReducidaAdmin {
    max-width: 6rem;
    overflow: hidden;
    font-size: 0.85rem !important;
}

.addIconAdmin {
    height: 2.5rem;
    cursor: pointer;
}

.contIconAdd {
    position: relative;
}

.contIconAdd:hover::before {
    content: "Agregar Producto";
    position: absolute;
    bottom: 0;
    left: 105%;
    bottom: 0;
    background-color: #11aa6a;
    border: 1px solid var(--color_bordes);
    padding: 5px;
    border-radius: 5px;
    color: white;
}

.celdaAdd {
    background-color: rgb(232, 255, 229) !important;
}

.newTr {
    height: 7rem !important;
    min-height: 7rem !important;
}

.inputCeldasAddProduct {
    overflow-wrap:break-word !important;
}

.contDeleteAdmin-cancel {
    bottom: 1rem;
}

.contDeleteAdmin-cancel:hover::before {
    content: "Cancelar";
    position: absolute;
    left: 80%;
    bottom: 100%;
    background-color: rgb(206, 66, 66);
    border: 1px solid var(--color_bordes);
    padding: 5px;
    border-radius: 5px;
    color: white;
    z-index: 100;
}

.contDeleteAdmin-Ok {
    position: absolute;
    bottom: unset;
    left: 0;
    right: 0;
    top: 1rem;
    margin: auto;
    height: 1.5rem;
}

.contDeleteAdmin-Ok:hover::before {
    content: "Confirmar";
    position: absolute;
    left: 80%;
    bottom: 100%;
    background-color: #11aa6a;
    border: 1px solid var(--color_bordes);
    padding: 5px;
    border-radius: 5px;
    color: white;
    z-index: 100;
}

.selectCategoria {
    background-color: white;
    color: var(--color_bordes);
}