.chatCont {
    position: fixed;
    right: 0;
    bottom: 0;
    height: fit-content;
    margin: 1rem;
    z-index: 500;
    max-width: 90vw;
}

.chatBox {
    position: relative;
    width: 40rem;
    height: 40rem;
    max-width: 100%;
    max-height: var(--altura_main);
    background-color: white;
    border: 3px solid rgb(56, 56, 161);
    border-radius: 5px;
}

.chatMensajesCont {
    width: 100%;
    height: 95%;
    padding: 0.5rem;
    color: black;
    overflow-y: auto;
    overflow-wrap: break-word;
    padding: 0rem 0.5rem;
}

.chatMensajesCont p {
    margin-bottom: 0;
}

.chatMensajeCont {
    width: 100%;
    height: 5%;
}

.chatInputMensaje {
    background-color: white;
    height: 100%;
    width: calc(100% - 5rem);
    border: 1px solid gray;
    color: black;
    padding: 0 0.5rem 0 0.5rem;
}

.chatButtonSend {
    height: 100%;
    width: 5rem;
    border-style: none;
    background-color: var(--color_naranja);
    border: 1px solid black;
}

.chatMensajeUsername {
    font-style: italic;
    color: var(--color_violeta);
}

.bubble {
    height: 7rem;
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    transform: rotateY(180deg);
    cursor: pointer;
    z-index: 500;
}

.bubble:hover {
    filter: brightness(150%);
}

.chatMinimize {
    position: absolute;
    height: 1.75rem;
    top: 0;
    right: 0;
    cursor: pointer;
    background-color: white;
}

.chatMensajeFecha {
    color: blue;
}

.chatMensajes {
    margin-top: 0.5rem;
}