.contGen {
    min-height: var(--altura_main);
}

.contGen input{
    border-style: none;
    background-color: white;
    margin: 0.5rem;
    padding: 0.75rem;
    width: 300px; 
    font-size: 1rem;
    font-weight: 300;
    color: gray;
}

.contGen input::placeholder {
    text-transform: uppercase;
}

.addFile {
    display: none;
}

.botonAddFile {
    width: 300px;
    font-size: 1rem;
    font-weight: 300;
    margin: 0.5rem 0;
    height: fit-content;
    border-style: none;
    background-color: #FAD688;
    color: black;
    padding: 0.5rem;
    border: 1px solid  rgb(74, 0, 117);
}

.botonEnviar {
    border-style: none;
    background-color: rgb(109, 0, 172);
    width: 200px;
    margin: 0.5rem;
    padding: 0.5rem;
    font-size: 1.1rem;
    margin-top: 1.5rem;
    height: 3.5rem;
    border-radius: 1.75rem;
    border: 1px solid rgba(255, 255, 255, 0.897);
}

.notaAvatar {
    text-align: center;
    width: 300px;
    font-weight: 300;
}
